import React, { useState, useContext } from "react";
import { UserContext } from "../../constants";
import cs from "./Login.module.css";

const Login = () => {
  const [username, setUsername] = useContext(UserContext);
  const [isPending, setPending] = useState(false);
  const onLogin = async e => {
    setPending(true);
    e.preventDefault();
    const data = new FormData(e.target);
    const name = await window.client.loginUser(data.get("username"));
    setPending(false);
    setUsername(name);
  };
  const onLogout = async e => {
    setPending(true);
    e.preventDefault();
    const name = await window.client.logoutUser();
    setPending(false);
    setUsername(name);
  };
  return (
    <section className={cs.content}>
      <h1>{username ? <>Hi, {username}</> : <>What's your name?</>}</h1>
      <form className={cs.form} onSubmit={onLogin}>
        <input
          required
          disabled={isPending}
          type="text"
          placeholder="username"
          defaultValue={username || undefined}
          name="username"
        />
        {username ? (
          <input
            disabled={isPending}
            onClick={onLogout}
            type="button"
            value="logout"
          />
        ) : null}
        <input
          disabled={isPending}
          type="submit"
          value={username ? "change" : "login"}
        />
      </form>
    </section>
  );
};

export default Login;
